import { useTranslation } from "react-i18next";
import { useSearchParams } from "react-router-dom";
import UniversalTab from "../../DepartmentHead/Competetions/CompetitionTab/UniversalTab";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import CombinedTeachingAllottment from "./CombinedTeachingAllottment";
import NonTeachingAllottment from "./NonTeachingAllottment";
const LeaveAllottmentConfig = ({
  carryParentState,
  instituteId,
  staffAlias,
}) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const tabList = [
    {
      name: t("combined_teaching"),
      id: 0,
      url: "member/leavestransfer?a=configuration",
      is_active: "enabled",
    },
    {
      name: t("non_teaching"),
      id: 1,
      url: "member/leavestransfer?a=configuration&d=nonteaching",
      is_active: "enabled",
    },
    {
      name: t("other"),
      id: 2,
      url: "member/leavestransfer?a=configuration&d=other",
      is_active: "disabled",
    },
  ];
  return (
    <>
      <UniversalTab
        tabList={tabList}
        activeIndexTab={!searchParams.get("d") ? 0 : 1}
        showAs={true}
        customStyleTab={{
          width: "100%",
        }}
        customStyleLink={{
          width: "33.3%",
        }}
        carryParentState={carryParentState}
      />
      <BorderBottom
        customStyle={{
          width: "100%",
          //   marginTop: "0.5rem",
        }}
      />

      {!searchParams.get("d") ? (
        <CombinedTeachingAllottment
          instituteId={instituteId}
          staffAlias={staffAlias}
        />
      ) : searchParams.get("d") === "nonteaching" ? (
        <NonTeachingAllottment
          instituteId={instituteId}
          staffAlias={staffAlias}
        />
      ) : null}
    </>
  );
};

export default LeaveAllottmentConfig;
