import { useEffect, useState } from "react";
import style from "../../PromoteDepartment/PromoteDepartment.module.css";
import JoinFormInput from "../../../../../JoiningForm/Student/Form/JoinFormInput";
import QLoader from "../../../../../Loader/QLoader";
import { useLeaveSetOffRule } from "../../../../../hooks/member_tab/department-api";
import { useTranslation } from "react-i18next";
const SetOffRuleConfig = ({ lid, onRefetch, leaveConfig }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    casual_leave: "",
    medical_leave: "",
    sick_leave: "",
    commuted_leave: "",
    maternity_leave: "",
    paternity_leave: "",
    quarantine_leave: "",
    half_pay_leave: "",
    study_leave: "",
    sabbatical_leave: "",
    special_disability_leave: "",
    winter_vacation_leave: "",
    summer_vacation_leave: "",
    child_adoption_leave: "",
    bereavement_leave: "",
    earned_leave: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [leaveSetOffRule] = useLeaveSetOffRule();

  useEffect(() => {
    if (leaveConfig) {
      setState({
        casual_leave: leaveConfig?.casual_leave ?? "",
        medical_leave: leaveConfig?.medical_leave ?? "",
        sick_leave: leaveConfig?.sick_leave ?? "",
        commuted_leave: leaveConfig?.commuted_leave ?? "",
        maternity_leave: leaveConfig?.maternity_leave ?? "",
        paternity_leave: leaveConfig?.paternity_leave ?? "",
        quarantine_leave: leaveConfig?.quarantine_leave ?? "",
        half_pay_leave: leaveConfig?.half_pay_leave ?? "",
        study_leave: leaveConfig?.study_leave ?? "",
        sabbatical_leave: leaveConfig?.sabbatical_leave ?? "",
        special_disability_leave: leaveConfig?.special_disability_leave ?? "",
        winter_vacation_leave: leaveConfig?.winter_vacation_leave ?? "",
        summer_vacation_leave: leaveConfig?.summer_vacation_leave ?? "",
        child_adoption_leave: leaveConfig?.child_adoption_leave ?? "",
        bereavement_leave: leaveConfig?.bereavement_leave ?? "",
        earned_leave: leaveConfig?.earned_leave ?? "",
      });
    }
  }, [leaveConfig]);
  const onUpdate = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onSave = () => {
    if (lid) {
      setDisabled((pre) => !pre);
      leaveSetOffRule({
        lid: lid,
        setConfig: state,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      {/* <div
          className={style.with_search}
          style={{
            marginTop: "1rem",
          }}
        >
          <h6>{t("leave_configuration")}</h6>
        </div> */}
      <div className={style.all_leave_config}>
        <JoinFormInput
          labelText={t("casual_leave")}
          placeholder={t("casual_leave_placeholder")}
          name={"casual_leave"}
          value={state?.casual_leave}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("medical_leave")}
          placeholder={t("medical_leave_placeholder")}
          name={"medical_leave"}
          value={state?.medical_leave}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("sick_leave")}
          placeholder={t("sick_leave_placeholder")}
          name={"sick_leave"}
          value={state?.sick_leave}
          type={"tel"}
          onChange={onUpdate}
        />
      </div>
      <div className={style.all_leave_config}>
        <JoinFormInput
          labelText={t("commuted_leave")}
          placeholder={t("commuted_leave_placeholder")}
          name={"commuted_leave"}
          value={state?.commuted_leave}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("maternity_leave")}
          placeholder={t("maternity_leave_placeholder")}
          name={"maternity_leave"}
          value={state?.maternity_leave}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("paternity_leave")}
          placeholder={t("paternity_leave_placeholder")}
          name={"paternity_leave"}
          value={state?.paternity_leave}
          type={"tel"}
          onChange={onUpdate}
        />
      </div>
      <div className={style.all_leave_config}>
        <JoinFormInput
          labelText={t("quarantine_leave")}
          placeholder={t("quarantine_leave_placeholder")}
          name={"quarantine_leave"}
          value={state?.quarantine_leave}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("half_pay_leave")}
          placeholder={t("half_pay_leave_placeholder")}
          name={"half_pay_leave"}
          value={state?.half_pay_leave}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("study_leave")}
          placeholder={t("study_leave_placeholder")}
          name={"study_leave"}
          value={state?.study_leave}
          type={"tel"}
          onChange={onUpdate}
        />
      </div>
      <div className={style.all_leave_config}>
        <JoinFormInput
          labelText={t("sabbatical_leave")}
          placeholder={t("sabbatical_leave_placeholder")}
          name={"sabbatical_leave"}
          value={state?.sabbatical_leave}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("special_disability_leave")}
          placeholder={t("special_disability_leave_placeholder")}
          name={"special_disability_leave"}
          value={state?.special_disability_leave}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("winter_vacation_leave")}
          placeholder={t("winter_vacation_leave_placeholder")}
          name={"winter_vacation_leave"}
          value={state?.winter_vacation_leave}
          type={"tel"}
          onChange={onUpdate}
        />
      </div>
      <div className={style.all_leave_config}>
        <JoinFormInput
          labelText={t("summer_vacation_leave")}
          placeholder={t("summer_vacation_leave_placeholder")}
          name={"summer_vacation_leave"}
          value={state?.summer_vacation_leave}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("child_adoption_leave")}
          placeholder={t("child_adoption_leave_placeholder")}
          name={"child_adoption_leave"}
          value={state?.child_adoption_leave}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("bereavement_leave")}
          placeholder={t("bereavement_leave_placeholder")}
          name={"bereavement_leave"}
          value={state?.bereavement_leave}
          type={"tel"}
          onChange={onUpdate}
        />
      </div>
      <div className={style.all_leave_config}>
        <JoinFormInput
          labelText={t("earned_leave")}
          placeholder={t("earned_leave_placeholder")}
          name={"earned_leave"}
          value={state?.earned_leave}
          type={"tel"}
          onChange={onUpdate}
        />
      </div>
      <button
        className={style.select_application_btn}
        style={{
          width: "100%",
          justifyContent: "center",
        }}
        onClick={onSave}
      >
        {t("save")}
      </button>
      {disabled && <QLoader />}
    </>
  );
};

export default SetOffRuleConfig;
