import { useEffect, useState } from "react";
import style from "../../PromoteDepartment/PromoteDepartment.module.css";
import QLoader from "../../../../../Loader/QLoader";
import { useLeaveCarryAcademicConfigRule } from "../../../../../hooks/member_tab/department-api";
import { useTranslation } from "react-i18next";
import TileWrapper from "../../../../../Department/Institute/Components/exportData/TileWrapper";
import JoinFormCalender from "../../../../../JoiningForm/Student/Form/JoinFormCalender";

const AllLeaveSetting = ({ lid, onRefetch, leaveConfigCarry }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    leave_carry_forward: "",
    leave_pre_approved: "",
    calendar: "",
  });

  const [disabled, setDisabled] = useState(false);
  const [leaveCarryAcademicConfigRule] = useLeaveCarryAcademicConfigRule();

  useEffect(() => {
    if (leaveConfigCarry) {
      setState({
        leave_carry_forward: leaveConfigCarry?.leave_carry_forward ?? "",
        leave_pre_approved: leaveConfigCarry?.leave_pre_approved ?? "",
      });
    }
  }, [leaveConfigCarry]);
  const onSave = () => {
    if (lid) {
      setDisabled((pre) => !pre);
      leaveCarryAcademicConfigRule({
        lid: lid,
        flow: state.calendar ? "Leave_Start" : "",
        year: state.calendar ? state.calendar?.substring(6) : "",
        date: state.calendar ? state.calendar?.substring(0, 2) : "",
        month: state.calendar ? state.calendar?.substring(3, 5) : "",
        int_month: state.calendar ? +state.calendar?.substring(3, 5) : "",
        cConfig: state,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onForwardTile = (val) => {
    setState((prev) => ({
      ...prev,
      leave_carry_forward: val,
    }));
  };
  const onApprovedTile = (val) => {
    setState((prev) => ({
      ...prev,
      leave_pre_approved: val,
    }));
  };

  const onDateGrab = (val) => {
    setState((prev) => ({
      ...prev,
      calendar: val,
    }));
  };

  return (
    <>
      <div className={style.all_leave_config}>
        <TileWrapper
          labelText="leave_carry_forward"
          tileList={tileList}
          activeTile={state.leave_carry_forward}
          onTile={onForwardTile}
        />
        <TileWrapper
          labelText="leave_pre_approved"
          tileList={tileList}
          activeTile={state.leave_pre_approved}
          onTile={onApprovedTile}
        />
        <JoinFormCalender
          labelText={t("select_leave_academic_year")}
          placeholder={t("select_date")}
          name={"calendar"}
          value={state.calendar}
          onDateFunction={onDateGrab}
          //  errorShow={formErrors["staffDOB"]}
          customStyleContainer={{ position: "relative", width: "48%" }}
          datePickerPosition={{
            top: "-1rem",
            left: "-7rem",
          }}
        />
      </div>
      <button
        className={style.select_application_btn}
        style={{
          width: "100%",
          justifyContent: "center",
        }}
        onClick={onSave}
      >
        {t("save")}
      </button>
      {disabled && <QLoader />}
    </>
  );
};

export default AllLeaveSetting;

const tileList = [
  {
    name: "yes",
    key: true,
  },
  {
    name: "no",
    key: false,
  },
];
