import { useTranslation } from "react-i18next";
import { useSearchParams } from "react-router-dom";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import LeaveAllottmentConfig from "./LeaveAllottmentConfig";
import AllLeaveConfig from "./AllLeaveConfig";
import SetOffRuleConfig from "./SetOffRuleConfig";
import { useLeaveConfigAllDetail } from "../../../../../hooks/member_tab/department-api";
import { useEffect } from "react";
import LeavesAndTransferConfigurationTabs from "./LeavesAndTransferConfigurationTabs";
import LeaveAndTransferHoliday from "./LeaveAndTransferHoliday";

const LeavesAndTransferConfiguration = ({
  carryParentState,
  instituteId,
  staffAlias,
  lid,
}) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const { leaveConfigAllDetail, leaveConfigAllDetailRefetch } =
    useLeaveConfigAllDetail({
      id: instituteId,
      skip: !instituteId,
    });

  useEffect(() => {
    if (instituteId) {
      leaveConfigAllDetailRefetch();
    }
  }, [leaveConfigAllDetailRefetch, instituteId]);

  // console.info("leaveConfigAllDetail", leaveConfigAllDetail);
  return (
    <>
      {!searchParams.get("n") ? (
        <LeavesAndTransferConfigurationTabs
          carryParentState={carryParentState}
        />
      ) : searchParams.get("n") === "allottment" ? (
        <>
          <h6>{t("leave_allottment")}</h6>
          <BorderBottom
            customStyle={{
              width: "100%",
              //   marginTop: "0.5rem",
            }}
          />
          <LeaveAllottmentConfig
            instituteId={instituteId}
            carryParentState={carryParentState}
            staffAlias={staffAlias}
          />
        </>
      ) : searchParams.get("n") === "allleave" ? (
        <>
          <h6>{t("all_leave")}</h6>
          <BorderBottom
            customStyle={{
              width: "100%",
              //   marginTop: "0.5rem",
            }}
          />
          <AllLeaveConfig
            instituteId={instituteId}
            carryParentState={carryParentState}
            lid={lid}
            leaveConfig={leaveConfigAllDetail?.leave_config?.staff_leave_config}
            leaveConfigCarry={{
              leave_carry_forward:
                leaveConfigAllDetail?.leave_config?.leave_carry_forward,
              leave_pre_approved:
                leaveConfigAllDetail?.leave_config?.leave_pre_approved,
            }}
            onRefetch={leaveConfigAllDetailRefetch}
          />
        </>
      ) : searchParams.get("n") === "setoff" ? (
        <>
          <h6>{t("leave_setoff_rule")}</h6>
          <BorderBottom
            customStyle={{
              width: "100%",
              //   marginTop: "0.5rem",
            }}
          />
          <SetOffRuleConfig
            lid={lid}
            carryParentState={carryParentState}
            leaveConfig={leaveConfigAllDetail?.leave_config?.set_off_rules}
            onRefetch={leaveConfigAllDetailRefetch}
          />
        </>
      ) : searchParams.get("n") === "holiday" ? (
        <>
          <h6>{t("holiday")}</h6>
          <BorderBottom
            customStyle={{
              width: "100%",
              //   marginTop: "0.5rem",
            }}
          />
          <LeaveAndTransferHoliday
            lid={lid}
            carryParentState={carryParentState}
            leaveConfig={leaveConfigAllDetail?.leave_config?.holiday_config}
            onRefetch={leaveConfigAllDetailRefetch}
          />
        </>
      ) : null}
    </>
  );
};

export default LeavesAndTransferConfiguration;
