import { useTranslation } from "react-i18next";
import { useSearchParams } from "react-router-dom";
import UniversalTab from "../../DepartmentHead/Competetions/CompetitionTab/UniversalTab";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import AllLeaveForm from "./AllLeaveForm";
import AllLeaveSetting from "./AllLeaveSetting";
const AllLeaveConfig = ({
  carryParentState,
  instituteId,
  lid,
  leaveConfig,
  leaveConfigCarry,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const tabList = [
    {
      name: t("all_leave"),
      id: 0,
      url: "member/leavestransfer?a=configuration&n=allleave",
      is_active: "enabled",
    },
    {
      name: t("leave_setting"),
      id: 1,
      url: "member/leavestransfer?a=configuration&n=allleave&d=setting",
      is_active: "enabled",
    },
  ];

  return (
    <>
      <UniversalTab
        tabList={tabList}
        activeIndexTab={!searchParams.get("d") ? 0 : 1}
        showAs={true}
        customStyleTab={{
          width: "100%",
        }}
        customStyleLink={{
          width: "50%",
        }}
        carryParentState={carryParentState}
      />
      <BorderBottom
        customStyle={{
          width: "100%",
          //   marginTop: "0.5rem",
        }}
      />

      {!searchParams.get("d") ? (
        <AllLeaveForm
          instituteId={instituteId}
          leaveConfig={leaveConfig}
          onRefetch={onRefetch}
        />
      ) : searchParams.get("d") === "setting" ? (
        <AllLeaveSetting
          lid={lid}
          leaveConfigCarry={leaveConfigCarry}
          onRefetch={onRefetch}
        />
      ) : null}
    </>
  );
};

export default AllLeaveConfig;
