import { useEffect, useState } from "react";
import style from "../../PromoteDepartment/PromoteDepartment.module.css";
import QLoader from "../../../../../Loader/QLoader";
import { useLeaveMarkHoliday } from "../../../../../hooks/member_tab/department-api";
import { useTranslation } from "react-i18next";
import TileWrapper from "../../../../../Department/Institute/Components/exportData/TileWrapper";
import JoinFormCalender from "../../../../../JoiningForm/Student/Form/JoinFormCalender";

const LeaveAndTransferHoliday = ({ lid, onRefetch, leaveConfigCarry }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    mark_status: "",
    date_arr: [],
  });

  const [disabled, setDisabled] = useState(false);
  const [leaveMarkHoliday] = useLeaveMarkHoliday();

  useEffect(() => {
    if (leaveConfigCarry) {
      setState({
        mark_status: leaveConfigCarry?.mark_status ?? "",
        date_arr: leaveConfigCarry?.date_arr ?? [],
      });
    }
  }, [leaveConfigCarry]);
  const onSave = () => {
    if (lid) {
      setDisabled((pre) => !pre);
      leaveMarkHoliday({
        lid: lid,
        holidayConfig: state,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onForwardTile = (val) => {
    setState((prev) => ({
      ...prev,
      mark_status: val ? "ALL_SUNDAYS" : "",
    }));
  };
  const onApprovedTile = (val) => {
    setState((prev) => ({
      ...prev,
      mark_status: val ? "ALL_SATURDAYS" : "",
    }));
  };

  const onDateGrab = (val) => {
    setState((prev) => ({
      ...prev,
      date_arr: val,
    }));
  };

  // console.info("sa;lkfdnbjhss", state.date_arr);

  return (
    <>
      <div className={style.all_leave_config}>
        <TileWrapper
          labelText="all_sundays"
          tileList={tileList}
          activeTile={state.mark_status === "ALL_SUNDAYS" ? true : false}
          onTile={onForwardTile}
        />
        <TileWrapper
          labelText="all_saturdays"
          tileList={tileList}
          activeTile={state.mark_status === "ALL_SATURDAYS" ? true : false}
          onTile={onApprovedTile}
        />
        <JoinFormCalender
          labelText={t("select_leave_holiday")}
          placeholder={t("select_date")}
          name={"calendar"}
          value={state.date_arr}
          onDateFunction={onDateGrab}
          //  errorShow={formErrors["staffDOB"]}
          customStyleInput={{
            paddingRight: "3rem",
          }}
          customStyleContainer={{ position: "relative", width: "48%" }}
          datePickerPosition={{
            top: "-1rem",
            left: "-7rem",
          }}
          isMultiple
        />
      </div>
      <button
        className={style.select_application_btn}
        style={{
          width: "100%",
          justifyContent: "center",
        }}
        onClick={onSave}
      >
        {t("save")}
      </button>
      {disabled && <QLoader />}
    </>
  );
};

export default LeaveAndTransferHoliday;

const tileList = [
  {
    name: "yes",
    key: true,
  },
  {
    name: "no",
    key: false,
  },
];
