import { useEffect, useState } from "react";
import style from "../../PromoteDepartment/PromoteDepartment.module.css";
import JoinFormInput from "../../../../../JoiningForm/Student/Form/JoinFormInput";
import QLoader from "../../../../../Loader/QLoader";
import { useUpdateMutlipleLeaveConfig } from "../../../../../hooks/member_tab/department-api";
import { useTranslation } from "react-i18next";

const AllLeaveForm = ({ instituteId, onRefetch, leaveConfig }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    c_l: "",
    m_l: "",
    s_l: "",
    cm_l: "",
    ma_l: "",
    pa_l: "",
    q_l: "",
    hp_l: "",
    std_l: "",
    sb_l: "",
    sd_l: "",
    wv_l: "",
    sv_l: "",
    ch_l: "",
    b_l: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [updateMutlipleLeaveConfig] = useUpdateMutlipleLeaveConfig();

  useEffect(() => {
    if (leaveConfig) {
      setState({
        c_l: leaveConfig?.casual_leave ?? "",
        m_l: leaveConfig?.medical_leave ?? "",
        s_l: leaveConfig?.sick_leave ?? "",
        cm_l: leaveConfig?.commuted_leave ?? "",
        ma_l: leaveConfig?.maternity_leave ?? "",
        pa_l: leaveConfig?.paternity_leave ?? "",
        q_l: leaveConfig?.quarantine_leave ?? "",
        hp_l: leaveConfig?.half_pay_leave ?? "",
        std_l: leaveConfig?.study_leave ?? "",
        sb_l: leaveConfig?.sabbatical_leave ?? "",
        sd_l: leaveConfig?.special_disability_leave ?? "",
        wv_l: leaveConfig?.winter_vacation_leave ?? "",
        sv_l: leaveConfig?.summer_vacation_leave ?? "",
        ch_l: leaveConfig?.child_adoption_leave ?? "",
        b_l: leaveConfig?.bereavement_leave ?? "",
      });
    }
  }, [leaveConfig]);
  const onUpdate = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: +e.target.value,
    }));
  };

  const onSave = () => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      updateMutlipleLeaveConfig({
        id: instituteId,
        leaveConfig: state,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      {/* <div
        className={style.with_search}
        style={{
          marginTop: "1rem",
        }}
      >
        <h6>{t("leave_configuration")}</h6>
      </div> */}
      <div className={style.all_leave_config}>
        <JoinFormInput
          labelText={t("casual_leave")}
          placeholder={t("casual_leave_placeholder")}
          name={"c_l"}
          value={state?.c_l}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("medical_leave")}
          placeholder={t("medical_leave_placeholder")}
          name={"m_l"}
          value={state?.m_l}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("sick_leave")}
          placeholder={t("sick_leave_placeholder")}
          name={"s_l"}
          value={state?.s_l}
          type={"tel"}
          onChange={onUpdate}
        />
      </div>
      <div className={style.all_leave_config}>
        <JoinFormInput
          labelText={t("commuted_leave")}
          placeholder={t("commuted_leave_placeholder")}
          name={"cm_l"}
          value={state?.cm_l}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("maternity_leave")}
          placeholder={t("maternity_leave_placeholder")}
          name={"ma_l"}
          value={state?.ma_l}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("paternity_leave")}
          placeholder={t("paternity_leave_placeholder")}
          name={"pa_l"}
          value={state?.pa_l}
          type={"tel"}
          onChange={onUpdate}
        />
      </div>
      <div className={style.all_leave_config}>
        <JoinFormInput
          labelText={t("quarantine_leave")}
          placeholder={t("quarantine_leave_placeholder")}
          name={"q_l"}
          value={state?.q_l}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("half_pay_leave")}
          placeholder={t("half_pay_leave_placeholder")}
          name={"hp_l"}
          value={state?.hp_l}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("study_leave")}
          placeholder={t("study_leave_placeholder")}
          name={"std_l"}
          value={state?.std_l}
          type={"tel"}
          onChange={onUpdate}
        />
      </div>
      <div className={style.all_leave_config}>
        <JoinFormInput
          labelText={t("sabbatical_leave")}
          placeholder={t("sabbatical_leave_placeholder")}
          name={"sb_l"}
          value={state?.sb_l}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("special_disability_leave")}
          placeholder={t("special_disability_leave_placeholder")}
          name={"sd_l"}
          value={state?.sd_l}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("winter_vacation_leave")}
          placeholder={t("winter_vacation_leave_placeholder")}
          name={"wv_l"}
          value={state?.wv_l}
          type={"tel"}
          onChange={onUpdate}
        />
      </div>
      <div className={style.all_leave_config}>
        <JoinFormInput
          labelText={t("summer_vacation_leave")}
          placeholder={t("summer_vacation_leave_placeholder")}
          name={"sv_l"}
          value={state?.sv_l}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("child_adoption_leave")}
          placeholder={t("child_adoption_leave_placeholder")}
          name={"ch_l"}
          value={state?.ch_l}
          type={"tel"}
          onChange={onUpdate}
        />
        <JoinFormInput
          labelText={t("bereavement_leave")}
          placeholder={t("bereavement_leave_placeholder")}
          name={"b_l"}
          value={state?.b_l}
          type={"tel"}
          onChange={onUpdate}
        />
      </div>
      <button
        className={style.select_application_btn}
        style={{
          width: "100%",
          justifyContent: "center",
        }}
        onClick={onSave}
      >
        {t("save")}
      </button>
      {disabled && <QLoader />}
    </>
  );
};

export default AllLeaveForm;
