import { useNavigate, useParams } from "react-router-dom";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { imageShowUrl } from "../../../../../services/BaseUrl";
import { useTranslation } from "react-i18next";
import style from "../LeavesAndTransfer.module.css";
const StaffTeachingCard = ({ staff, staffAlias }) => {
  const { t } = useTranslation();
  const params = useParams();
  const navigate = useNavigate();

  const onNavigate = () => {
    navigate(`/q/${params.username}/staff/profile`, {
      state: {
        sid: staff?._id,
        viewAs: "DEPARTMENT_HEAD",
      },
    });
  };

  return (
    <>
      <div
        className={style.staff_card}
        onClick={onNavigate}
        style={{
          cursor: "pointer",
        }}
      >
        <div className={style.staff_card_info}>
          <img
            src={
              staff?.staffProfilePhoto
                ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                : "/images/member_tab/class/default_avatar.svg"
            }
            loading="lazy"
            alt="avatar"
          />

          <div>
            <h6 className={style.staff_card_info_inner}>
              {`${staff?.staffFirstName ?? ""} ${
                staff?.staffMiddleName ?? ""
              } ${staff?.staffLastName ?? ""}`}
            </h6>
            <div className={style.staff_card_info_inner_index}>
              {staffAlias ? staffAlias : t("index")}
              {": "}
              {staff?.staffROLLNO ?? ""}
            </div>
          </div>
        </div>
      </div>
      <BorderBottom
        customStyle={{
          width: "100%",
        }}
      />
    </>
  );
};

export default StaffTeachingCard;
