import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { debounce } from "lodash";
import { useScrollbar } from "../../../../../Scroll/use-scroll";
import style from "../../StaffAdmisionAdmin/AdmissionScholarship/AdmissionScholarship.module.css";
import { useLeaveAllottmentStaffList } from "../../../../../hooks/member_tab/department-api";
import StaffTeachingCard from "./StaffTeachingCard";
import QvipleLoading from "../../../../../Loader/QvipleLoading";

const NonTeachingAllottment = ({ instituteId, staffAlias }) => {
  const [state, setState] = useState(true);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [ref, inView] = useInView({ skip: search });
  const [allStaff, setAllStaff] = useState([]);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const { scrollToTop } = useScrollbar();

  const { leaveAllottmentStaffList, leaveAllottmentStaffListRefetch } =
    useLeaveAllottmentStaffList({
      data: {
        id: instituteId,
        page: page,
        limit: 10,
        type: "Non-Teaching Faculty",
        search: search,
      },
      skip: !instituteId,
    });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (instituteId) {
      setShowingDataLoading(true);
      leaveAllottmentStaffListRefetch();
    }
  }, [page, instituteId, search, leaveAllottmentStaffListRefetch]);

  useEffect(() => {
    if (leaveAllottmentStaffList?.all_staff) {
      if (search) {
        setAllStaff(() => leaveAllottmentStaffList?.all_staff);
        setShowingDataLoading(false);
      } else {
        setAllStaff((prev) =>
          [
            ...new Set(
              [...prev, ...leaveAllottmentStaffList?.all_staff].map(
                JSON.stringify
              )
            ),
          ].map(JSON.parse)
        );
        setShowingDataLoading(false);
      }
    }
    //for next api is call or not dashboard
    if (leaveAllottmentStaffList?.all_staff.length === 10) setState(true);
    else setState(false);
  }, [leaveAllottmentStaffList?.all_staff]);

  const onDebounce = useCallback(
    debounce((val) => setSearch(val), 1000),
    []
  );

  const onSearchEvent = (e) => {
    if (!e.target.value) setPage(1);
    onDebounce(e.target.value);
  };

  useEffect(() => {
    scrollToTop();
  }, [scrollToTop]);

  return (
    <>
      <section className={style.search_container}>
        <div
          className={style.search_container_input}
          style={{
            width: "100%",
          }}
        >
          <img
            className={style.searchIcon}
            alt="search icon"
            src="/images/search-dash-icon.svg"
          />
          <input
            type="text"
            placeholder="Search staff..."
            onChange={onSearchEvent}
            style={{
              width: "100%",
              border: "0.5px solid #cacaca",
            }}
          />
        </div>
      </section>

      {allStaff?.map((staff, index) =>
        allStaff?.length === index + 1 ? (
          <div ref={ref} key={staff?._id}>
            <StaffTeachingCard staff={staff} staffAlias={staffAlias} />
          </div>
        ) : (
          <StaffTeachingCard
            key={staff?._id}
            staff={staff}
            staffAlias={staffAlias}
          />
        )
      )}
      {showingDataLoading && <QvipleLoading />}
    </>
  );
};

export default NonTeachingAllottment;
